.button-controls {
  margin-top: 30px;
  display: flex;
  align-items: center;
}

.btn {
  padding: 13px 0;
  min-width: 100px;
  margin: 0 10px;
  border: 0;
  background-color: var(--ABSOLUTE-ZERO);
  color: #ffffff;
  border-radius: 4px;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
}

.btn:hover {
  background-color: var(--BRANDEIS-BLUE);
}

.btn:active {
  transform: scale(1.15);
}

.btn:focus {
  outline-color: var(--VIVID-SKY-BLUE);
}

