.header h1 {
    color: #ffffff;
}

@media screen and (max-width: 700px) {
    .header h1 {
        font-size: 1.5rem;
        text-align: center;
    }
}
.quote-container {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0.5rem;
  color: var(--WHITE);
}

.quote-text {
  font-size: 1.5rem;
  font-weight: 800;
  color: #f7f7ff;
  padding: 0 3rem;
  font-family: "Text Me One", sans-serif;
}

.quote-text::first-letter {
  font-weight: bold;
  font-size: 1.8rem;
}

.quote-author {
  font-size: 1.2rem;
  color: #fffccc;
  font-weight: 800;
  text-align: center;
}
.quote-author::before {
  content: "--";
  margin-right: 0.3rem;
}

@media screen and (max-width: 700px) {
  .quote-container {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    padding: 0 0.5rem;
    text-align: center;
  }
  .quote-text {
    font-size: 1.1rem;
    padding: 0 0.3rem;
  }
  .quote-author {
    font-size: 1rem;
  }
}

.button-controls {
  margin-top: 30px;
  display: flex;
  align-items: center;
}

.btn {
  padding: 13px 0;
  min-width: 100px;
  margin: 0 10px;
  border: 0;
  background-color: var(--ABSOLUTE-ZERO);
  color: #ffffff;
  border-radius: 4px;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
}

.btn:hover {
  background-color: var(--BRANDEIS-BLUE);
}

.btn:active {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}

.btn:focus {
  outline-color: var(--VIVID-SKY-BLUE);
}

@media screen and (max-width: 700px) {
}

.settings h2 {
    font-size: 1.1rem;
    color: #ffffff;
}

.settings label {
    display: inline-block;
    font-size: 1rem;
    color: #ffffff;
}

.settings input[type="number"] {
    border: 0;
    font-size: 1rem;
    -moz-appearance: textfield;
}

.settings input[type="number"]::-webkit-inner-spin-button,
.settings input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.input-control {
    padding: 1rem;
}

.input-group {
    margin-top: 0.4rem;
}

.input-control input[type="button"] {
    padding: 0.6rem 1rem;
    font-size: 1.1rem;
    border: 0;
    font-weight: bold;
    color: #ffffff;
    background-color: var(--BRANDEIS-BLUE);
}

.input-control input[type="button"]:focus {
    background-color: var(--ABSOLUTE-ZERO);
    color: #ffffff;
    outline: 0;
}

.settings input[type="number"] {
    outline: 0;
    padding: 0.62rem 0;
    font-weight: bold;
    text-align: center;
    color: var(--ABSOLUTE-ZERO);
    vertical-align: top;
}

.settings input[type="number"]:focus {
    background-color: #ffffff;
    color: var(--ABSOLUTE-ZERO);
}

.settings .input-group:focus-within {
    box-shadow: 3px 3px 4px 2px var(--VIVID-CERULEAN);
    outline: 0;
}

.settings input[type="checkbox"] {
    opacity: 0;
    position: absolute;
    left: -999px;
}

.settings .custom-checkbox {
    height: 1.6rem;
    width: 1.6rem;
    margin-right: 0.9rem;
    border: 1px solid var(--NATIONS-BLUE);
    border-radius: 3px;
    cursor: pointer;
    position: relative;
    background-color: #ffffff;
    display: inline-block;
    vertical-align: middle;
}

.settings .custom-checkbox:hover {
    box-shadow: 0 0 2px 2px var(--VIVID-SKY-BLUE);
}

.settings input[type="checkbox"]:checked+.custom-checkbox {
    background-color: var(--BRANDEIS-BLUE);
    box-shadow: none;
}

.settings input[type="checkbox"]:checked+.custom-checkbox:hover {
    box-shadow: 0 0 2px 2px var(--VIVID-CERULEAN);
}

.settings input[type="checkbox"]:checked+.custom-checkbox::before {
    content: "";
    display: inline-block;
    height: 1rem;
    width: 0.35rem;
    border-bottom: 0.16rem solid #ffffff;
    border-right: 0.16rem solid #ffffff;
    position: relative;
    left: 0.6rem;
    top: 0.2rem;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.settings input[type="checkbox"]:focus-within+.custom-checkbox {
    box-shadow: 0 0 3px 3px var(--VIVID-CERULEAN);
}

.alaram-sound,
.auto-start {
    padding-top: 20px;
}

input[type="checkbox"] {
    opacity: 0;
}

@media screen and (max-width: 700px) {
    .input-control {
        display: inline-block;
        padding-left: 1.7rem;
    }
    .settings h2 {
        font-size: 1rem;
        color: #ffffff;
        text-align: center;
    }
}
@font-face {
    font-family: "digital";
    src: url(/pomodoro-timer/static/media/digital-webfont.5b0f802e.woff) format("woff2"), url(/pomodoro-timer/static/media/digital-webfont.1edd89f1.woff2) format("woff");
    font-weight: normal;
    font-style: normal;
}

.pomodoro-container {
    flex: 1.5 1;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.clock-ring-one {
    height: 290px;
    width: 290px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    box-shadow: 0 0 1px 1px hsla(202, 100%, 50%, 0.2);
    margin: 0 auto;
}

.clock-ring-two {
    height: 245px;
    width: 245px;
    border: 2px solid hsla(202, 100%, 50%, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.clock-ring-three {
    height: 220px;
    width: 220px;
    border: 2.5px solid var(--VIVID-CERULEAN);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.clock-time {
    letter-spacing: 5px;
    font-size: 3rem;
    color: #fff;
    box-shadow: 0 0 1px 1px hsla(202, 100%, 50%, 0.2);
    height: 180px;
    width: 180px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "digital", Arial, Helvetica, sans-serif;
}

.button-controls {
    margin-top: 30px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

@media screen and (max-width: 700px) {
    .pomodoro-container {
        display: block;
    }
    .button-controls {
        padding-bottom: 1rem;
        margin-top: 1rem;
    }
}
.notifications {
  position: absolute;
  left: 0.2rem;
  bottom: 0.5rem;
}

.show-msg {
  -webkit-animation: show-msg 4s ease;
          animation: show-msg 4s ease;
  margin: 5px 10px;
  padding: 0.3rem 0.6rem;
  text-align: center;
  background-color: var(--VIVID-CERULEAN);
  color: #ffffff;
  border-radius: 5px;
  opacity: 0;
  font-size: 1rem;
}

@-webkit-keyframes show-msg {
  0% {
    opacity: 1;
  }
  20% {
    opacity: 0.8;
  }
  40% {
    opacity: 0.6;
  }
  60% {
    opacity: 0.4;
  }
  80% {
    opacity: 0.2;
  }
  100% {
    opacity: 0;
  }
}

@keyframes show-msg {
  0% {
    opacity: 1;
  }
  20% {
    opacity: 0.8;
  }
  40% {
    opacity: 0.6;
  }
  60% {
    opacity: 0.4;
  }
  80% {
    opacity: 0.2;
  }
  100% {
    opacity: 0;
  }
}

.todo-container {
    position: relative;
    padding-left: 6rem;
    width: 45vw;
    min-height: 50vh;
}

.todo-container h1 {
    position: absolute;
    top: -6rem;
    padding: 1.2rem 0;
    background-color: #ffffff;
    width: 45vw;
    text-align: center;
    color: var(--ABSOLUTE-ZERO);
}

.add-todo form {
    padding: 0.3rem 0;
    text-align: center;
}

.add-todo form input[type="text"] {
    padding: 12px;
    border-radius: 0.2rem;
    margin: 0 0.3rem;
    width: 60%;
    border: 0.06rem solid var(--ABSOLUTE-ZERO);
    font-size: 1.1rem;
    color: var(--ABSOLUTE-ZERO);
}

.add-todo form input[type="text"]:focus {
    box-shadow: 0 0 0.2rem 0.06rem var(--ABSOLUTE-ZERO);
}

.add-todo form input[type="submit"] {
    padding: 0.8rem 1rem;
    background-color: var(--ABSOLUTE-ZERO);
    border-color: transparent;
    color: #ffffff;
    border-radius: 0.2rem;
}

.add-todo form input[type="submit"]:hover {
    background-color: var(--BRANDEIS-BLUE);
}

.add-todo form input[type="submit"]:focus {
    box-shadow: 0 0 0.1rem 0.1rem var(--VIVID-SKY-BLUE);
}

.todo-filter {
    text-align: center;
}

.todo-filter input[type="button"] {
    border: 0;
    outline-color: var(--VIVID-SKY-BLUE);
    margin: 0.2rem;
    padding: 0.3rem 0.6rem;
    font-size: 1rem;
    cursor: pointer;
    background-color: #e4e1e1;
    border-radius: 0.2rem;
}

.todo-items li {
    list-style-type: none;
    font-size: 1.2rem;
    margin: 0.4rem;
    padding: 0.7rem;
    width: 80%;
    /* box-shadow: 0 -0.01rem 0.01rem 0 var(--ABSOLUTE-ZERO); */
}

.todo-items li:hover {
    border-radius: 0.3rem;
    background-color: rgba(0, 162, 255, 0.2);
}

.completed-task {
    text-decoration: line-through;
    -webkit-text-decoration-color: var(--ABSOLUTE-ZERO);
            text-decoration-color: var(--ABSOLUTE-ZERO);
}

.todo-items input[type="checkbox"] {
    /* display: none; */
    opacity: 0;
    position: absolute;
    left: -999px;
}

.todo-items span {
    box-shadow: 0 0 0.06rem 0.06rem var(--ABSOLUTE-ZERO);
    border-radius: 0.2rem;
    position: relative;
    margin-right: 0.6rem;
    height: 1.3rem;
    width: 1.3rem;
    display: inline-block;
    vertical-align: middle;
}

.todo-items input[type="checkbox"]:checked+span {
    background-color: var(--ABSOLUTE-ZERO);
}

.todo-items input[type="checkbox"]:checked+span::before {
    content: "";
    position: absolute;
    border-left: 0.15rem solid #ffffff;
    border-bottom: 0.15rem solid #ffffff;
    width: 0.7rem;
    height: 0.3rem;
    top: 0.4rem;
    left: 0.2rem;
    -webkit-transform: rotate(80deg);
    transform: rotate(315deg);
}

.todo-items input[type="checkbox"]:focus-within+span {
    box-shadow: 0 0 3px 3px var(--VIVID-CERULEAN);
}

.remove-btn {
    float: right;
    border-color: transparent;
    font-weight: bold;
    background-color: transparent;
    color: #b22222;
    outline-color: transparent;
}

.remove-btn:hover,
.remove-btn:focus {
    background-color: #b22222;
    color: #ffffff;
    border-radius: 0.2rem;
}

.no-tasks {
    text-align: center;
    color: var(--ABSOLUTE-ZERO);
    align-items: center;
    padding-top: 3rem;
    font-size: 1.2rem;
}

@media screen and (max-width: 700px) {
    .todo-container {
        padding: 0;
        width: auto;
    }
    .todo-container h1 {
        position: static;
        width: auto;
        font-size: 1.5rem;
    }
    .todo-items ul {
        margin: 0;
        padding: 0;
    }
    .todo-items li {
        list-style-type: none;
        font-size: 1rem;
        margin: 0.4rem;
        padding: 0.5rem;
        width: auto;
    }
}
:root {
  --BRANDEIS-BLUE: #0c5ee8;
  --NATIONS-BLUE: #538cec;
  --ABSOLUTE-ZERO: #0949b5;
  --GAMBOGE: #eba10c;
  --VIVID-SKY-BLUE: #00dcf5;
  --VIVID-CERULEAN: #00a2ff;
  --WHITE: #ffffff;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

.container {
  height: 90vh;
  padding-left: 6rem;
  background-color: #00dcf5;
  background-color: var(--VIVID-SKY-BLUE);
  background: -webkit-linear-gradient(
    top,
    #0949b5,
    #00a2ff);
  background: linear-gradient(
    180deg,
    #0949b5,
    #00a2ff);
  background: -webkit-linear-gradient(
    top,
    var(--ABSOLUTE-ZERO),
    var(--VIVID-CERULEAN)
  );
  background: linear-gradient(
    180deg,
    var(--ABSOLUTE-ZERO),
    var(--VIVID-CERULEAN)
  );
  display: flex;
  flex-flow: column;
}

.content {
  display: flex;
}

@media screen and (max-width: 700px) {
  .content {
    display: block;
  }
  .container {
    height: auto;
    padding: 0;
  }
}

